private val LayoutScopeLocal: ProvidableCompositionLocal<Any?> = compositionLocalOf<Any?> { null }

@Composable
private fun <T : Any> currentLayoutScope(): T {
    @Suppress("UNCHECKED_CAST")
    return LayoutScopeLocal.current as? T
        ?: error("Unexpected between Page/Layout scope and the parent Layout which it is providing it. Please report this issue to the Kobweb developers.")
}

@Composable
private fun <T : Any> provideLayoutScope(layoutScope: T, content: @Composable () -> Unit) {
    androidx.compose.runtime.CompositionLocalProvider(LayoutScopeLocal provides layoutScope, content = content)
}

private fun forceReloadNow() {
    window.stop()
    window.location.reload()
}

private fun handleServerStatusEvents() {
    val status = document.getElementById("status")!!
    var lastVersion: Int? = null
    var shouldReload = false

    val warningIcon = status.children[0]!!
    val spinnerIcon = status.children[1]!!
    val statusText = status.children[2]!!

    status.addEventListener("transitionend", {
        if (status.hasClass("fade-out")) {
            status.removeClass("fade-out")
            if (shouldReload) {
                forceReloadNow()
            }
        }
    })

    val eventSource = EventSource("/api/kobweb-status", EventSourceInit(true))
    eventSource.addEventListener("version", { evt ->
        val version = (evt as MessageEvent).data.toString().toInt()
        if (lastVersion == null) {
            lastVersion = version
        }
        if (lastVersion != version) {
            lastVersion = version
            if (document.asDynamic().hidden) {
                // Reload immediately when the page is not visible as the animation will not run
                forceReloadNow()
            } else if (status.className.isNotEmpty()) {
                shouldReload = true
            } else {
                // Not sure if we can get here but if we can't rely on the status transition
                // to reload we should do it ourselves.
                forceReloadNow()
            }
        }
    })

    eventSource.addEventListener("status", { evt ->
        val values: dynamic = JSON.parse<Any>((evt as MessageEvent).data.toString())
        val text = values.text as String
        val isError = (values.isError as String).toBoolean()
        if (text.isNotBlank()) {
            warningIcon.className = if (isError) "visible" else "hidden"
            spinnerIcon.className = if (isError) "hidden" else "visible"
            statusText.innerHTML = "<i>$text</i>"
            status.className = "fade-in"
        } else {
            if (status.className == "fade-in") {
                status.className = "fade-out"
            }
        }
    })

    eventSource.onerror = { eventSource.close() }
}

public fun main() {
    handleServerStatusEvents()

    window.api.logOnError = true

    AppGlobals.initialize(mapOf("title" to "Optimumtech"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.registerLayout(
            "optimum_tech.org.components.layouts.MarkdownLayout",
            parentLayoutId = "optimum_tech.org.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                optimum_tech.org.components.layouts.initMarkdownLayout(ctx)
            },
        ) { pageCtx, pageMethod -> 
            _optimum_tech_org_components_layouts_MarkdownLayout {
                pageMethod(pageCtx)
            }
        }
        ctx.router.registerLayout(
            "optimum_tech.org.components.layouts.PageLayout",
        ) { pageCtx, pageMethod -> 
            _optimum_tech_org_components_layouts_PageLayout(pageCtx) {
                provideLayoutScope(this) {
                    pageMethod(pageCtx)
                }
            }
        }
        ctx.router.register(
            "/",
            layoutId = "optimum_tech.org.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                optimum_tech.org.pages.initHomePage(ctx)
            },
        ) { pageCtx -> 
            _optimum_tech_org_pages_HomePage()
        }
        ctx.router.register(
            "/about",
            layoutId = "optimum_tech.org.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                optimum_tech.org.pages.about.initAboutPage(ctx)
            },
        ) { pageCtx -> 
            _optimum_tech_org_pages_about_About()
        }
        ctx.router.register(
            "/apps",
            layoutId = "optimum_tech.org.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                optimum_tech.org.pages.apps.initAppsPage(ctx)
            },
        ) { pageCtx -> 
            _optimum_tech_org_pages_apps_Apps()
        }
        ctx.router.register(
            "/contact",
            layoutId = "optimum_tech.org.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                optimum_tech.org.pages.contacts.initContactPage(ctx)
            },
        ) { pageCtx -> 
            _optimum_tech_org_pages_contacts_Contact()
        }
        ctx.router.register(
            "/services",
            layoutId = "optimum_tech.org.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                optimum_tech.org.pages.services.initServicesPage(ctx)
            },
        ) { pageCtx -> 
            _optimum_tech_org_pages_services_Services()
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text", optimum_tech.org.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text", optimum_tech.org.SubheadlineTextStyle)
        ctx.theme.registerStyle("markdown", optimum_tech.org.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("footer", optimum_tech.org.components.sections.FooterStyle)
        ctx.theme.registerStyle("nav-bar", optimum_tech.org.components.sections.NavBarStyle)
        ctx.theme.registerStyle("nav-container", optimum_tech.org.components.sections.NavContainerStyle)
        ctx.theme.registerStyle("logo", optimum_tech.org.components.sections.LogoStyle)
        ctx.theme.registerStyle("cta-button", optimum_tech.org.components.sections.CTAButtonStyle)
        ctx.theme.registerStyle("nav-link", optimum_tech.org.components.sections.NavLinkStyle)
        ctx.theme.registerStyle("hero-section", optimum_tech.org.pages.HeroSectionStyle)
        ctx.theme.registerStyle("hero-container", optimum_tech.org.pages.HeroContainerStyle)
        ctx.theme.registerStyle("hero-text", optimum_tech.org.pages.HeroTextStyle)
        ctx.theme.registerStyle("hero-title", optimum_tech.org.pages.HeroTitleStyle)
        ctx.theme.registerStyle("gradient-text", optimum_tech.org.pages.GradientTextStyle)
        ctx.theme.registerStyle("hero-description", optimum_tech.org.pages.HeroDescriptionStyle)
        ctx.theme.registerStyle("button-group", optimum_tech.org.pages.ButtonGroupStyle)
        ctx.theme.registerStyle("primary-button", optimum_tech.org.pages.PrimaryButtonStyle)
        ctx.theme.registerStyle("secondary-button", optimum_tech.org.pages.SecondaryButtonStyle)
        ctx.theme.registerStyle("demo-grid", optimum_tech.org.pages.DemoGridStyle)
        ctx.theme.registerStyle("grid-card", optimum_tech.org.pages.GridCardStyle)
        ctx.theme.registerStyle("main-card", optimum_tech.org.pages.MainCardStyle)
        ctx.theme.registerStyle("services-section", optimum_tech.org.pages.ServicesSectionStyle)
        ctx.theme.registerStyle("services-grid", optimum_tech.org.pages.ServicesGridStyle)
        ctx.theme.registerStyle("service-card", optimum_tech.org.pages.ServiceCardStyle)
        ctx.theme.registerStyle("service-icon", optimum_tech.org.pages.ServiceIconStyle)
        ctx.theme.registerStyle("background-decor", optimum_tech.org.pages.BackgroundDecorStyle)
        ctx.theme.registerStyle("purple-blur", optimum_tech.org.pages.PurpleBlurStyle)
        ctx.theme.registerStyle("pink-blur", optimum_tech.org.pages.PinkBlurStyle)
        ctx.theme.registerVariant("-circle", optimum_tech.org.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", optimum_tech.org.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("side-menu-slide-in", optimum_tech.org.components.sections.SideMenuSlideInAnim)
        optimum_tech.org.initColorMode(ctx)
        optimum_tech.org.initStyles(ctx)
        optimum_tech.org.initSiteStyles(ctx)
        optimum_tech.org.initTheme(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        optimum_tech.org.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
